<script>
	import { dialogConfirm, dialogConfirmHasbtn } from '../../config/mUtils'
	import {getInvesterManageListApi, getInvesterManageDetailApi,getUserlistApi,addInvesterManageInfoApi,getInvestorInvestGroupFundSelectListApi } from '../../config/api'
	
	export default {
		name: 'investerDetail',
		inject: ['reload'],
		data() {
			return {
				// 表单字段开始
				company_id: '',//如果investor_class == 2(管理公司)或者母基金、自管基金、子基金类型时 记录的id
				
				is_main: '',
				name: '',
				cell: '',
				email: '',
				company: '',
				document_type: '',
				document_no: '',
				document_expire: '',
				otherdoc_info: '',
				relate_investor: [],
				investor_type: '',
				
				investor_class: '',
				investor_class_name: '',
				investor_class_list: [],
				
				partner_type: '',
				ir_id: '',
				manager_id: '',
				creater_id: '',
				place: '',
				sex: '',
				birthday: '',
				address: '',
				status: 2,
				subscribe_num: '',
				mult: '',
				evaluate: '',
//				risk_type: '',
				document_time: '',
				phone: '',
				
				rmb_subscribe_money: '',
				rmb_real_fee: '',
				rmb_return_fee: '',
				rmb_income: '',
				dollar_subscribe_money: '',
				dollar_real_fee: '',
				dollar_return_fee: '',
				dollar_income: '',

				account_name: '',
				account_bankno: '',
				account_bank: '',
				subbranch: '',
				subbranch_prevince: '',
				subbranch_city: '',
				// 表单字段结束
				manage_company_list: [ ],
				
				allUserList: [],
				allInvestorList:[],
				isDisabledEditable: true,
				is_master_investor_list: [
					{
						id: 1,
						name: '是'
					},
					{
						id: 2,
						name: '否'
					},
				],
				sexList: [
					{
						label: '男',
						value: 1
					},
					{
						label: '女',
						value: 2
					}
				],
				// 投资人身份
				investerIdentity: [
					{
						value: 1,
						label: '普通'
					},
					{
						value: 2,
						label: '专业'
					}
				],
				// 投资人性质
				investerCharacter: [
					{
						value: 1,
						label: '自然人'
					},
					{
						value: 2,
						label: '机构'
					},
				],
				identityList: [
					{
						value: 1,
						label: '身份证'
					},
					{
						value: 2,
						label: '护照'
					},
					{
						value: 3,
						label: '其他国家证件'
					},
				],
			}
		},
		created() {
			this.id = this.$route.query.id;
			this.getInvesterManageDetail();
			this.getAllUserList();
			this.getReationPersonList();
		},
		mounted() {},
		methods: {
			getInvesterManageDetail(){
				getInvesterManageDetailApi({
					id: this.id
				}).then(res => {
					//console.log(res);
					if(res.data.msgNo == 1) {
						this.initFormData(res.data.payload);
						// console.log(this.investor_class);
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			addInvesterManageInfo(){
				var relate_investor = this.relate_investor.join(',');
				var info = {
					company_id: this.company_id,
					is_main: this.is_main,
					id: this.id,
					name: this.name,
					cell: this.cell,
					email: this.email,
					company: this.company,
					phone: this.phone,
					document_type: this.document_type,
					document_no: this.document_no,
					document_expire: this.document_expire,
					otherdoc_info: this.otherdoc_info,
					relate_investor: relate_investor,
					investor_type: this.investor_type,
					partner_type: this.partner_type,
					investor_class: this.investor_class,
					ir: this.ir_id,
					manager: this.manager_id,
					creater: this.creater_id,
					place: this.place,
					sex: this.sex,
					birthday: this.birthday,
					address: this.address,
					open_status: this.status,
					
					account_name: this.account_name,
					account_bankno: this.account_bankno,
					account_bank: this.account_bank,
					subbranch: this.subbranch,
					subbranch_prevince: this.subbranch_prevince,
					subbranch_city: this.subbranch_city,
				}
				addInvesterManageInfoApi({
					info: info
				}).then(res => {
					//console.log(res);
					if(res.data.msgNo == 1) {
						dialogConfirm("success", res.data.message);
						this.reload()
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			// 获取所有人员列表
			getAllUserList(){
				getUserlistApi({
					number: 10000,
					page: 1
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.allUserList = res.data.payload.data;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			changeHideStatus(e) {
				if (!this.isDisabledEditable) {
					if(e == 1) {
						this.status = 1;
					} else {
						this.status = 2;
					}
				}
			},
			initFormData(obj){
				if (obj.relate_investor) {
					var relate_investor = obj.relate_investor.split(',');
				} else{
					var relate_investor = [];
				}
				relate_investor = this.translateStingToNumber(relate_investor);

				this.company_id = obj.company_id;
				this.name = obj.name;
				this.is_main = obj.is_main;
				this.evaluate = obj.evaluate;
				this.cell = obj.cell;
				this.email = obj.email;
				this.company = obj.company;
				this.document_type = obj.document_type;
				this.document_no = obj.document_no;
				this.document_expire = obj.document_expire;
				this.otherdoc_info = obj.otherdoc_info;
				this.relate_investor = relate_investor;
				this.investor_type = obj.investor_type;
				this.investor_class = obj.investor_class;
				this.investor_class_name = obj.investor_class_name;
				
				
				this.partner_type = obj.partner_type;
				this.ir_id = obj.ir_id;
				this.manager_id = obj.manager_id;
				this.creater_id = obj.creater_id;
				this.place = obj.place;
				this.sex = obj.sex;
				this.birthday = obj.birthday;
				this.address = obj.address;
				this.status = obj.open_status;
				this.subscribe_num = obj.subscribe_num;
				this.rmb_subscribe_money = obj.rmb_subscribe_money;
				this.rmb_real_fee = obj.rmb_real_fee;
				this.rmb_return_fee = obj.rmb_return_fee;
				this.rmb_income = obj.rmb_income;
				this.dollar_subscribe_money = obj.dollar_subscribe_money;
				this.dollar_real_fee = obj.dollar_real_fee;
				this.dollar_return_fee = obj.dollar_return_fee;
				this.dollar_income = obj.dollar_income;
				this.mult = obj.mult;
				this.account_name = obj.account_name;
				this.account_bankno = obj.account_bankno;
				this.account_bank = obj.account_bank;
				this.subbranch = obj.subbranch;
				this.subbranch_prevince = obj.subbranch_prevince;
				this.subbranch_city = obj.subbranch_city;
//				this.risk_type = obj.risk_type;
				this.document_time = obj.document_time;
				this.phone = obj.phone;
				if (this.investor_class) {
					this.getFundSelectList(this.investor_class)
				}
			},
			getFundSelectList(e){
				var type = '';
				if (e == 1) {
					type = 1;
				} else if(e == 2){
					type = '';
				} else if(e == 3){
					type = 2;
				} else if(e == 4){
					type = 4
				} else {
					type = 3
				}
				if (type) {
					getInvestorInvestGroupFundSelectListApi({
						type: type
					}).then(res => {
						if(res.data.msgNo == 1) {
							this.manage_company_list = res.data.payload;
						} else {
							dialogConfirm("info", res.data.message);
						}
					})
				}
			},
			translateStingToNumber(arr){
				for (var i=0,max=arr.length;i<max;i++) {
					arr[i] = parseInt(arr[i]);
				}
				return arr;
			},
			changeEditStatus() {
				this.isDisabledEditable = false;
//				this.$nextTick(function () {
//		        		this.$refs.input1.$el.querySelector('input').focus();
//		        });
			},
			chooseInvestorName(e){
				if (e) {
					var item = this.getSelectedItem(e);
					this.name = item.name;
				}
			},
			// 返回上一个列表页面
			cancel() {
				this.reload()
//				this.$router.go(-1);
			},


			//去基金运行
			toFundRunPage(){
				this.$router.push({
					name: "investerManageInvestGroup",
					query: {
						id: this.id,
					}
				})
			},
			getReationPersonList(){
				getInvesterManageListApi({
					page: 1,
					pageSize: 10000,
					name:'',
					ir: '',
					manager: ''
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.allInvestorList = res.data.payload.data;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			}
		},
	}
</script>

<style scoped="scoped">
	.company {
		box-sizing: border-box;
		padding: 0 30px;
		font-size: 14px;
	}
	
	.company_header {
		color: #6292F4;
		border-bottom: 1px solid #EAEAEA;
	}
	
	.company_header span {
		display: inline-block;
		padding: 0 15px 10px 15px;
		border-bottom: 2px solid #6292F4;
	}
	
	.company_name {
		padding-bottom: 20px;
		border-bottom: 1px solid #EAEAEA;
	}
	
	.company_name_left {
		font-size: 16px;
		color: #33302D;
	}
	
	.company_name_right {
		margin-left: 80px;
		color: #6292F4;
	}
	
	.company_name_right i,
	.company_name_right span {
		color: #6292F4;
	}
	
	.company_name_right button {
		width: 115px;
		height: 30px;
		margin-left: 37px;
		color: white;
		font-size: 14px;
		border-radius: 20px;
		background: #6292F4;
	}
	
	.form {
		flex-wrap: wrap;
		padding: 30px 0;
		color: #6F7071;
	}
	.form_title{
		font-size: 16px;
		color: #333;
	}
	.form_list {
		width: 40%;
		display: inline-flex;
		margin-bottom: 19px;
	}
	
	.form_list_left {
		width: 155px;
		color: #6F7071;
	}
	
	.form_list_right {
		width: 260px;
		height: 40px;
	}
	
	.radio_btn {
		width: 73px;
		height: 40px;
		margin-right: 38px;
		line-height: 40px;
		text-align: center;
		border-radius: 20px;
		background: #F3F6FB;
	}
	
	.radio_btn_active {
		color: white;
		background: #6292F4 !important;
	}
	.form_submit{
		margin-bottom: 20px;
	}
	.form_submit button {
		width: 94px;
		height: 36px;
		color: #6292F4;
		font-size: 14px;
		margin-right: 26px;
		border-radius: 20px;
		border: 1px solid #6292F4;
	}
	
	.form_submit button:nth-child(2) {
		color: white;
		background: #6292F4;
	}
	.line {
        width: 100%;
        height: 1px;
        background: #F4F4F4;
        margin: 0px 0 40px 0;
    }
</style>


